import React from 'react';

const Card = ({name, email, id}) => {

    return (
         <div className='tc bg-light-green dib br3 ma2 grow bw2 shadow5' >
        <img src = {`https://robohash.org/${id}?100X100`} alt = 'robot' />
        <div >
        < h3 > {name}</h3> 
        <p> {email }</p> 
        </div > 
        </div>
    );
}

export default Card;